body {
  background-color: #e9ebf0;
  font-family: Helvetica, Arial;
}

.wrapper {
  margin-top: 50px;
  background-color: #fff;
  border-radius: 15px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
}
.wrapper p {
  font-style: italic;
  color: #666;
}

.wrapper label {
  display: block;
}
.wrapper input {
  padding: 3px 6px;
}

.amount-input {
  margin-bottom: 10px;
}

.wrapper hr {
  margin: 40px auto;
}

@media only screen and (min-width: 1400px) {
  .wrapper {
  width: 50%;
  }
  .wrapper input {
  width: 600px;
  }
}

@media only screen and (max-width: 1399px) and (min-width: 750px) {
  .wrapper {
  width: 75%;
  }
  .wrapper input {
  width: 400px;
  }
}
 
.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.convert-box {
  display:flex;
  flex-direction:row;
  justify-content: space-around;
}
 
 
 